/* Reset & base */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #121212;
  color: #e0e0e0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fs-app {
  max-width: 900px;
  margin: 1rem auto;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* HEADER */

.fs-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #333;
  padding-bottom: 1rem;
  flex-wrap: wrap;
}

.fs-brand {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.fs-logo {
  background: #0ea5e9; /* bright light blue */
  color: #121212;
  font-weight: 900;
  font-size: 1.8rem;
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fs-brand h1 {
  margin: 0;
  font-weight: 700;
  font-size: 1.8rem;
  user-select: none;
}

.fs-sub {
  font-weight: 400;
  font-size: 0.9rem;
  color: #a0a0a0;
  margin-top: 2px;
  user-select: none;
}

.fs-controls {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex-wrap: wrap;
  margin-top: 0.5rem;
}

.fs-search {
  padding: 0.4rem 0.75rem;
  font-size: 1rem;
  border-radius: 16px;
  border: none;
  background-color: #212121;
  color: #eee;
  outline-offset: 2px;
  outline-color: #0ea5e9;
  min-width: 220px;
}

.fs-search::placeholder {
  color: #666;
}

.fs-btn {
  background-color: #0ea5e9;
  border: none;
  color: #121212;
  font-weight: 700;
  padding: 0.5rem 1rem;
  border-radius: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
}

.fs-btn:hover {
  background-color: #0b7dc1;
}

/* MAIN LAYOUT */

.fs-main {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

/* CHECKLIST */

.fs-checklist {
  flex: 1 1 400px;
  max-width: 600px;
}

.fs-empty {
  padding: 1rem;
  text-align: center;
  color: #888;
  font-style: italic;
}

.fs-card {
  background-color: #1f1f1f;
  border-radius: 18px;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.25rem;
  box-shadow: 0 0 8px rgba(14, 165, 233, 0.1);
  transition: background-color 0.3s ease;
  user-select: none;
}

.fs-card.done {
  background-color: #0ea5e9;
  color: #121212;
}

.fs-left {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.fs-icon {
  font-size: 2.2rem;
  user-select: none;
}

.fs-meta {
  display: flex;
  flex-direction: column;
}

.fs-title {
  font-weight: 700;
  font-size: 1.1rem;
  user-select: text;
}

.fs-time {
  font-weight: 400;
  font-size: 0.85rem;
  color: #87cefa; /* lighter blue time */
  user-select: none;
}

/* CHECK BUTTON */

.fs-check {
  width: 38px;
  height: 38px;
  border-radius: 12px;
  border: 2px solid #0ea5e9;
  background-color: transparent;
  cursor: pointer;
  font-size: 20px;
  line-height: 1;
  color: transparent;
  transition: all 0.3s ease;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.fs-check:hover {
  background-color: rgba(14, 165, 233, 0.15);
}

.fs-check.checked {
  background-color: #0ea5e9;
  color: rgba(255, 255, 255, 0.6); /* lighter white tick */
  font-weight: bold;
  transition: color 0.3s ease;
}

/* FOOTER ACTIONS */

.fs-footer-actions {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.fs-tertiary {
  background: none;
  border: none;
  color: #0ea5e9;
  cursor: pointer;
  font-weight: 700;
  font-size: 0.9rem;
  padding: 0;
  user-select: none;
}

.fs-tertiary:hover {
  text-decoration: underline;
}

.fs-note {
  font-size: 0.8rem;
  color: #888;
  user-select: none;
}

/* ASIDE */

.fs-aside {
  flex: 1 1 220px;
  max-width: 260px;
  font-size: 0.9rem;
  color: #ccc;
  user-select: none;
}

.fs-aside h3 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-weight: 700;
  color: #0ea5e9;
}

.fs-small {
  font-size: 0.8rem;
  line-height: 1.4;
  margin-bottom: 1rem;
}
